import React from 'react';
import Storm from '../Storm.png'
import './Team.css'
import { Card, Image } from 'semantic-ui-react'

const Members = [
    {
        name: 'STORM#2856',
        avatar: Storm,
        id: '299895531701010442',
    },
    {
        name: '블랙카이#2583',
        avatar: 'https://cdn.discordapp.com/avatars/734560292024877056/6e6a68e216a05ea4d15f72226f6981b5.png?size=256',
        id: '734560292024877056',
    },
]

function Team() {
    return (
        <div className='Team'>
            <h1 className='Developer'>카이봇 개발자</h1>
            <Card.Group itemsPerRow={1} stackable>
                {Members.map(({ name, avatar, id }) => (
                    <Card href={`https://discord.com/users/${id}`} target='_blank' key={id}>
                        <Card.Content className='CardContent'>
                            <Image src={avatar} alt={name} size='tiny' circular floated='left' />
                            <Card.Header>{name}</Card.Header>
                        </Card.Content>
                    </Card>
                ))}
            </Card.Group>
        </div>
    );
}

export default Team;
